import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AddPackingComponent } from './add-packing.component';
import { Routes, RouterModule } from '@angular/router';

import { NgSelectModule } from '@ng-select/ng-select';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PackingService } from 'src/app/service/packing.service';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { ProductListPopupModule } from 'src/app/common-module/product-list-popup/product-list-popup.module';

const routes: Routes = [
  {
    path: '',
    component: AddPackingComponent
  }
]

@NgModule({
  declarations: [AddPackingComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    FormsModule, ReactiveFormsModule,
    FormValidationModule,
    SharedModule,
    NgSelectModule,
    ProductListPopupModule,
  ], providers: [PackingService]
})
export class AddPackingModule { }
