import { Component, OnInit, ViewChild } from '@angular/core';
import { ProductListPopupComponent } from 'src/app/common-module/product-list-popup/product-list-popup.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonFunction } from 'src/app/common';
import { PackingService } from 'src/app/service/packing.service';

@Component({
  selector: 'app-add-packing',
  templateUrl: './add-packing.component.html',
})
export class AddPackingComponent implements OnInit {

  @ViewChild(ProductListPopupComponent, { static: false }) productAdd;
  orderNumberArray: any = [];
  packTypeArray: any = [];
  orderDetails: any;

  loadingState = false;
  addForm: FormGroup;
  showLoader = false;
  formErrors = {
    apierror: null,
    order_id: null,
    product: null
  };
  isEditing = false;
  editId = null;
  productDetailList: any = []

  constructor(
    private packingService: PackingService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private router: Router,
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
    }
    this.addForm = this.fb.group({
      sales_order_id: [null, Validators.compose([Validators.required])],
      pack_type: [null, Validators.compose([Validators.required])],
      remark: [null],
      batch_no: [null],
      serial_no: [null],
      cartoon_no: [null],
      product_detail_list: [],
    });



  }

  ngOnInit(): void {
    // if (this.isEditing) {
    //   this.getEditObject()
    // }
    this.getMasterData();
  }
  getMasterData() {
    this.packingService.getMasterData({}).subscribe((responce) => {
      if (responce.success) {
        this.orderNumberArray = responce.data.sales_order ? responce.data.sales_order : [];
        this.packTypeArray = responce.data.pack_type ? responce.data.pack_type : [];
      }
    });
  }

  onOrderChange(data) {
    if (data && data.value) {
      this.getSalesOrderDetail(data.value);
      this.formErrors.order_id = null;
    }
  }
  getSalesOrderDetail(id) {
    this.packingService.getSalesOrderDetail(id).subscribe((responce) => {
      if (responce.success) {
        this.orderDetails = responce.data;

      }
    });
  }

  addProduct() {
    if (this.addForm.value.sales_order_id) {
      this.productAdd.addObject();
    } else {
      this.formErrors.order_id = "* Please Select Order Number";
    }
  }
  onProductSubmit(data) {
    this.productDetailList = data;
    // if (data && data.length > 0) {
    //   data.filter((obj) => {
    //     if (obj.tag_list && obj.tag_list.length > 0) {
    //       obj.tag_list.filter((data, i) => {
    //         data['id'] = i;
    //         this.productDetailList.push(data)
    //       });
    //     }
    //   });
    // }
  }
  submitForm(formData: any): void {
    if (formData.valid) {
      if (this.productDetailList && this.productDetailList.length > 0) {
        let product_detail_list = []

        this.productDetailList.map((obj) => {
          let object = new Object();
          object['product_id'] = obj.product.value;
          object['weight_unit'] = obj.product.weight_unit;
          object['weight'] = obj.product.weight;
          object['product_qty'] = obj.product_qty;
          object['tag_id'] = obj.tag.value;
          product_detail_list.push(object);
        });

        this.formErrors.product = null;
        this.showLoader = true;
        const data = new FormData();
        data.append('sales_order_id', formData.value.sales_order_id);
        data.append('pack_type', formData.value.pack_type);
        data.append('remark', formData.value.remark);
        data.append('batch_no', formData.value.batch_no);
        data.append('serial_no', formData.value.serial_no);
        data.append('cartoon_no', formData.value.cartoon_no);
        data.append('product_detail_list', JSON.stringify(product_detail_list));
        if (this.isEditing) {
          this.packingService.editPacking(this.editId, data).subscribe((response) => {
            this.showLoader = false;
            if (response.success) {
              this.router.navigateByUrl('/outbound/pick-list-new');
            } else {
              response.error.map(obj => {
                if (obj.hasOwnProperty('sales_order_id')) {
                  this.formErrors['sales_order_id'] = obj['sales_order_id'];
                } else {
                  this.formErrors['apierror'] = `* ${response.error}`;
                }
              });
            }
          }, (error) => {
            this.showLoader = false;
          });
        } else {
          this.packingService.addPacking(data).subscribe((response) => {
            this.showLoader = false;
            if (response.success) {
              this.router.navigateByUrl('/outbound/pick-list-new');
            } else {
              response.error.map(obj => {
                if (obj.hasOwnProperty('sales_order_id')) {
                  this.formErrors['sales_order_id'] = obj['sales_order_id'];
                } else {
                  this.formErrors['apierror'] = `* ${response.error}`;
                }
              });
            }
          }, (error) => {
            this.showLoader = false;
          });
        }
      } else {
        this.formErrors.product = "* Please Add Product";
      }
    }
  }



}
